h1 {
  padding: 1rem 1.5rem;
  text-align: center;
  font-size: 20px;
  margin-bottom: 0px;

}

p {
  font-size: 12px;
}


.card {
  margin: 10px;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
          box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  display: table;
  float: left;

}

.card-holder:after {
  content: "";
  display: table;
  clear: both;
}

.card h1 {
  font-weight: bolder;
}

.card:hover {
  -webkit-box-shadow: 0 10px 20px 0 rgba(0,0,0,0.2);
          box-shadow: 0 10px 20px 0 rgba(0,0,0,0.2);
}

.card-img {
  padding-top: 15px;
  margin-top: 5px;
  float: left;
  margin: auto;
  margin-bottom: 0;
  text-align: center;
  width: auto;
}

.card-img #follow {
  margin-top: 5px;
  margin-right: 15px;
  color: white;
  background-color: #49AFC7;
}

.card-img Button:hover {
  margin-top: 5px;
  margin-right: 15px;
  color: white;
  background-color: #49AFA3;
}

.cardcontainer {
  padding: 5px;
  background-color: #E9F6FC;
  color: black;
  float: left;
  max-width: 250px;
  text-align: center;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;

}

img {
  width: 70px;
  left: 50%;
  margin: auto;
  text-align: center;
  display: inline-block;
}



